table {
  width: 100%; }

p {
  margin: 1rem 0;
  text-align: left; }

ul {
  margin: 1em; }

ul li {
    text-align: left; }

ul li p {
      margin: 0; }

@media only screen and (min-width: 768px) {
  ul {
    margin: 1em 0 1em 40px; } }

blockquote {
  margin: 1rem 0; }

blockquote p::first-letter {
    font-size: 110%; }

h1, h2, h3, h4, h5, h6, strong {
  font-weight: bold;
  margin: 2.33em 0;
  outline: 0; }

h1 {
  font-size: 2rem; }

h2 {
  font-size: 1.8rem; }

h3 {
  font-size: 1.6rem; }

h4 {
  font-size: 1.4rem; }

h5 {
  font-size: 1.2rem; }

h6 {
  font-size: 1rem; }

code {
  border: none !important; }

dl {
  margin: 1rem 0; }

dt {
  margin-top: 1rem;
  font-weight: bold; }

dd {
  font-size: 90%; }

dd p {
  margin: 0; }

abbr {
  text-decoration: none;
  font-style: italic;
  font-family: serif;
  cursor: help; }

figure {
  margin: 2.33em 0;
  text-align: center; }

figure img {
    margin: 0;
    border: 1px solid #f8f8ff;
    border-radius: 2px;
    padding: 1px; }

figure figcaption {
    clear: left;
    margin-top: 5px;
    text-align: center;
    font-size: 90%;
    font-style: italic;
    line-height: 0rem; }

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  opacity: 1; }

#app.spinning {
  opacity: 0.4; }

.slider {
  position: fixed;
  background: #fafafa;
  width: 250px;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 99999;
  padding: 10px;
  overflow-y: auto; }

.slider .close {
    float: right;
    cursor: pointer; }

.slider ul {
    list-style: none;
    margin: .25em;
    padding: 0; }

.slider a {
    padding: 0;
    text-transform: capitalize;
    font-size: 12px;
    line-height: 18px; }

.slider-toggle {
  position: fixed;
  width: 60px;
  height: 60px;
  z-index: 99999;
  cursor: pointer; }

.toc .navbar.is-fixed-top .navbar-menu {
  max-height: calc(100vh - 5.8rem); }

ul.toc {
  background-color: #fefefe;
  margin: 0;
  padding-left: 15px; }

ul.toc li {
    display: none;
    margin: 0; }

ul.toc li.h1 {
      font-size: 1rem; }

ul.toc li.h2 {
      display: block;
      font-weight: bold;
      font-size: 1rem; }

ul.toc li.h3 {
      display: inline-block;
      font-size: 1rem;
      padding-left: 10px;
      font-weight: normal; }

@media screen and (min-width: 1024px) {
  .toc .navbar-dropdown {
    max-height: calc(100vh - 5.8rem);
    overflow: auto; }
  ul.toc li.h2 {
    font-weight: normal; }
  ul.toc li.h3 {
    display: block; } }

.main {
  padding: 2rem 1rem; }

.main h1[id]:before, .main h2[id]:before, .main h3[id]:before {
    display: block;
    content: " ";
    margin-top: -110px;
    height: 110px;
    visibility: hidden; }

.main #write {
    max-width: 40em;
    margin: 0 auto;
    padding-top: 2em 60px;
    min-height: calc(100vh - 6em); }

.main summary {
    outline: 0;
    cursor: pointer; }

.main input[type=checkbox] {
    margin-right: 8px; }

.main .red {
    color: red; }

.main [title] {
    position: relative;
    display: inline; }

.main [title]:after {
      content: attr(title);
      color: #fff;
      background: #333;
      background: rgba(51, 51, 51, 0.9);
      padding: 5px 10px;
      position: absolute;
      left: -9999px;
      opacity: 0;
      bottom: 100%;
      width: 350px;
      -webkit-transition: 0.25s linear opacity; }

.main [title]:hover:after {
      left: 5px;
      opacity: 1; }

.main .notification {
    background-color: #f5f5f5; }

@media only screen and (min-width: 768px) {
  .main {
    padding: 2rem 3rem; } }

