.footer {
  font-size: 90%;
  font-style: italic; }

.footer p {
  text-align: center; }

.swiper-container {
  height: 450px; }

.swiper-container .swiper-wrapper {
    margin: 0; }

.swiper-container .swiper-wrapper .swiper-slide {
      list-style: none;
      text-align: center; }

.swiper-container .swiper-wrapper .swiper-slide img {
        border: 4px solid #eee;
        max-width: 520px;
        max-height: 350px;
        margin-top: 20px; }

